@use '../base/variables' as *;

.formulario {
    margin-top: 2rem;
    p {
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;

        span {
            font-weight: $bold;
        }
    }

    legend {        
        font-size: 2rem;
        color: $grisOscuro;
    }

    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
    textarea{
        height: 20rem;
        width: 100%;
    }
    .forma-contacto{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"]{
        margin: 0;
        width: auto;
    }
    
}
.u-max-40 {
  max-width: 40rem;
  margin-left: auto;
  margin-right: auto;
}

.u-center-block {
    display: block;
    margin-left: auto;
    margin-right: auto;
}
